import React, { Component } from "react";
import { Waypoint } from "react-waypoint";
import Revealable, { Revealable_right } from "../Components/Revealable";
import styled from "styled-components";

/*
 * Animation for revealing a section when scrolled onto.. using Waypoint (2nd phase)
 */

class Reveal extends Component {
  state = {
    revealed: false
  };
  reveal = () => {
    this.setState({ revealed: true });
  };
  unreveal = () => {
    this.setState({ revealed: false });
  };

  render() {
    return (
      <Waypoint onEnter={this.reveal} onLeave={this.unreveal}>
        <Revealable show={this.state.revealed}>
          {this.props.children}
        </Revealable>
      </Waypoint>
    );
  }
}

export default Reveal;

class Reveal_right extends Component {
  state = {
    revealed: false
  };
  reveal = () => {
    this.setState({ revealed: true });
  };
  unreveal = () => {
    this.setState({ revealed: false });
  };

  render() {
    return (
      <Waypoint onEnter={this.reveal} onLeave={this.unreveal}>
        <Revealable_right show={this.state.revealed}>
          {this.props.children}
        </Revealable_right>
      </Waypoint>
    );
  }
}
export { Reveal_right };
